.container {
  height: 400px;
  border-style: solid;
  flex-direction: column;
  margin: 2vh 10vw;
  display: flex;
}

.word {
  animation: 8s cubic-bezier(1, -.01, .63, .47) infinite forwards throwing;
}

#row1 {
  animation-delay: 0s;
}

#row2 {
  animation-delay: 1s;
}

#row3 {
  animation-delay: 2s;
}

#row4, #row5 {
  animation-delay: 0s;
}

@keyframes throwing {
  from {
    opacity: 0;
    margin-left: 0%;
  }

  5% {
    opacity: 1;
  }

  50% {
    margin-left: 75%;
  }

  to {
    margin-left: 75%;
  }
}

